export const checkBottom = ( el ) => {
    const rect = el.getBoundingClientRect();

    return rect.bottom <= window.innerHeight;
}

export const createCookie = ( name, value, days ) => {
    let expires = "";
    if ( days ) {
        const date = new Date();
        date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
        expires = `; expires=${date.toUTCString()}`;
    }
    document.cookie = `${name}=${value}${expires}; path=/`;
};

export const readCookie = ( name ) => {
    const nameEQ = `${name}=`;
    return document.cookie
        .split('; ')
        .find(( cookie ) => cookie.startsWith(nameEQ))
        ?.split('=')[1] || null;
};

export const deleteCookie = ( name ) => {
    createCookie(name, '', -1);
}

export const wclFetch = async ( data ) => {
    const token = await getToken();

    data.set('token', token);

    return fetch(config.ajax_url, {
        method: 'POST',
        body: data,
        credentials: 'same-origin',
    });
}

export const wclAdminFetch = async ( data ) => {
    return fetch(config.ajax_url, {
        method: 'POST',
        body: data,
        credentials: 'same-origin',
    });
}

export const getToken = async ( action = 'submit' ) => {
    return await grecaptcha.execute(config.recaptcha_key, { action: 'submit' });
}

export const updateURLParameters = ( paramsToUpdate ) => {
    const url = new URL(window.location.href);
    const params = new URLSearchParams(url.search);

    for ( const [ param, value ] of Object.entries(paramsToUpdate) ) {
        if ( param === 'make' && value !== null && value !== '0' ) {
            params.delete('model');
        }

        if ( value === null || value === '0' ) {
            params.delete(param); // Remove the parameter if the value is empty or null
        } else {
            params.set(param, value); // Update or add a new parameter
        }
    }

    const newUrl = params.toString() ? `${url.pathname}?${params.toString()}` : url.pathname

    history.pushState(null, '', newUrl);
};

export const gallerySwiperInit = () => {
    const galleries = document.querySelectorAll('.wcl-product-gallery');

    galleries.forEach(gallery => {
        const topSwiper = gallery.querySelector('.topSwiper');
        const thumbsSwiper = gallery.querySelector('.thumbsSwiper');

        const swiper = new Swiper(thumbsSwiper, {
            spaceBetween: 15,
            slidesPerView: 3,
            freeMode: true,
            watchSlidesProgress: true,
            grabCursor: true
        });

        const swiper2 = new Swiper(topSwiper, {
            spaceBetween: 10,
            grabCursor: true,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            thumbs: {
                swiper: swiper,
            },
        });
    });
}

export const selectedCurrency = ( $format = 'text' ) => {
    const selectedIndex = document.getElementById('currency').selectedIndex;
    const selectedOption = document.getElementById('currency').options[selectedIndex];

    if ( $format === 'text' ) {
        return selectedOption.text;
    } else {
        return selectedOption.value;
    }
}

export const wclT = ( key, value ) => {
    const keys = key.split('.');

    let message = keys.reduce(( obj, k ) => (obj && obj[k] !== undefined) ? obj[k] : undefined, i18n);

    if ( !message ) {
        return;
    }

    if ( value !== undefined ) {
        message = message.replace('{n}', value); // Замінюємо плейсхолдер {n} на значення
    }

    return message;
}

export const wclMessageTemplates = ( message, placeholders ) => {
    const replacements = {};

    for ( const [ key, value ] of Object.entries(placeholders) ) {
        const placeholder = `{{ ${key} }}`;
        replacements[placeholder] = value !== undefined ? value : '';
    }

    return Object.keys(replacements).reduce(( msg, placeholder ) => {
        return msg.replace(new RegExp(placeholder, 'g'), replacements[placeholder]);
    }, message);
}