import { wclAdminFetch } from "../helpers";

document.addEventListener('DOMContentLoaded', () => {
    const button = document.getElementById('get-car-information');

    if ( button ) {
        button.addEventListener('click', async e => {
            e.preventDefault();

            const postId = document.getElementById('post_ID').value;
            const postStatus = document.getElementById('post_status').value;
            const registrationNumber = document.querySelector('div[data-name="registration_number"] input');

            if ( postStatus !== 'draft' && postStatus !== 'publish' ) {
                alert('You need to save the post before receiving the information');
                return
            }

            if ( !registrationNumber || !registrationNumber.value ) {
                alert('Registration number not found');
                return
            }

            button.classList.add('loading');

            const data = new FormData();
            data.append('action', 'get_car_information');
            data.append('post_id', postId);
            data.append('registration_number', registrationNumber.value);

            try {
                const response = await wclAdminFetch(data);

                if ( !response.ok ) {
                    throw new Error('Unable to fetch data');
                }

                const result = await response.json();

                if ( result.success == '1' ) {
                    if ( confirm('Data received successfully. Reload page?') ) {
                        document.location.reload();
                    }
                }
            } catch ( error ) {
                console.error(error);
            } finally {
                button.classList.remove('loading');
            }
        });
    }
});